/* Global Rules */
* {
  padding: 0;
  margin: 0;
}
*,
*::before,
*::after {
  box-sizing: border-box;
}
body {
  font-family: 'Nunito Sans', sans-serif;
}
.container {
  margin-left: auto;
  margin-right: auto;
  padding-left: 15px;
  padding-right: 15px;
}
a {
  text-decoration: none;
}
ul {
  list-style: none;
}
/* Small */
@media (min-width: 768px) {
  .container {
    width: 750px;
  }
}
/* Medium */
@media (min-width: 992px) {
  .container {
    width: 970px;
  }
}
/* Large */
@media (min-width: 1200px) {
  .container {
    width: 1170px;
  }
}
main {
  background-color: #fafafa;
  transition: background-color 0.3s;
}
.dark {
  background-color: #202d36;
}
/* nav bar */
nav {
  box-shadow: 0px 5px 7px #dddddd9c;
  background-color: white;
  transition: 0.3s;
}
.dark nav {
  box-shadow: 0px 5px 7px #0a06289c;
  background-color: #2b3743;
  color: white;
}
nav .container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 60px;
}
nav .container button {
  display: flex;
  align-items: center;
  gap: 5px;
  border: none;
  background-color: transparent;
  cursor: pointer;
}
.dark nav .container button {
  color: white;
}
nav .container button p {
  font-weight: 400;
  text-transform: capitalize;
  transition: 0.3s;
}
/* Form */
form .container {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding-block: 30px;
  flex-wrap: wrap;
}
form .container .search-bar {
  height: 40px;
  display: flex;
  justify-content: center;
  align-items: center;
  padding-inline: 15px;
  background-color: white;
  box-shadow: 0px 0px 9px 0px #dddddd9c;
  gap: 10px;
  transition: 0.3s;
}
.dark form .container .search-bar {
  background-color: #2b3743;
  box-shadow: 0px 0px 9px 0px #0a06289c;
}
form .container .search-bar svg {
  color: #cccccc;
}
form .container .search-bar input {
  border: none;
  height: 40px;
  outline: none;
  width: 240px;
  max-width: 100%;
  transition: 0.3s;
}
.dark form .container .search-bar input {
  color: white;
  background-color: #2b3743;
}
form .container .search-bar input::placeholder {
  color: #6b6b6b;
  font-size: 0.7rem;
  transition: color 0.3s;
}
.dark form .container .search-bar input::placeholder {
  color: white;
}
.react-select__control {
  width: 500px;
}
/* Content */

.country-parent {
  padding-bottom: 50px;
}
.country-parent .container {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(250px, 1fr));
  gap: 45px 35px;
}
.country-parent .container .box {
  box-shadow: 0px 0px 9px 0px #dddddd9c;
  background-color: white;
  overflow: hidden;
  transition: 0.3s;
}
.dark .country-parent .container .box {
  box-shadow: 0px 0px 9px 0px #0a06289c;
  background-color: #2b3743;
}
.country-parent .container .box > img {
  width: 100%;
  height: 180px;
}
.country-parent .container .box > .text {
  padding: 20px 20px 35px 20px;
}

.country-parent .container .box > .text h4 {
  font-size: 1.1rem;
  color: black;
  transition: 0.3s;
}
.dark .country-parent .container .box > .text h4 {
  color: white;
}
.country-parent .container .box > .text .info {
  margin-top: 15px;
}
.country-parent .container .box > .text .info > p {
  display: flex;
  gap: 10px;
  font-weight: bold;
  color: #474748;
  text-transform: capitalize;
  transition: 0.3s;
}
.dark .country-parent .container .box > .text .info > p {
  color: white;
}
.country-parent .container .box > .text .info > p > p {
  font-weight: normal;
  transition: 0.3s;
}
.dark .country-parent .container .box > .text .info > p > p {
  color: rgb(216, 216, 216);
}
